"use client";
import { motion } from "framer-motion";
import Image from "next/image";

export default function OnBoardingImage() {
  return (
    <div>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
        className="relative h-full overflow-hidden flex items-center justify-center "
      >
        {/* Background image */}
        <motion.div
          initial={{ opacity: 0, scale: 1.05 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 1.2, ease: "easeOut" }}
          className="absolute top-0 left-0 h-full overflow-hidden z-0 w-full"
        >
          <Image
            src="/images/getstarted-leftside2.png"
            alt="Background"
            fill
            className="object-fill"
            priority
          />
        </motion.div>

        {/* Foreground images stacked vertically */}
        <div className="relative z-10 flex flex-col items-center justify-center space-y-12">
          <motion.img
            src="/images/guy.png"
            alt="Guy"
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.6, duration: 0.8, ease: "easeOut" }}
            className="object-contain w-[33%] h-auto -ml-32"
          />
          <motion.img
            src="/images/earn.png"
            alt="Earn"
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.9, duration: 0.8, ease: "easeOut" }}
            className="object-contain w-[70%] h-auto -ml-24"
          />
        </div>
      </motion.div>
    </div>
  );
}
